import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId,  ObjectKeys, fetchEncryptStream, convertArray2Obj, convertArray2ObjGroupby, hasWritePrivilege } from "../../../stores/Utils";

import {SearchInput_Cmpt, TabsHRM_Cmpt, ButtonImageRound_Cmpt } from "../../../components";

import { OpenModal } from "../../../components/DialogHRM";

import "./dashboard.css"
import { AddProduct_Cmpt, ProductItem_Cmpt, AddEntryProduct_Cmpt, AddOutProduct_Cmpt } from "..";
import {   search_product, loadCSVInve, rangeProdArr,  } from "../services/server_fetching";
import { get_balances_by_type } from "../../CuentasContables/services/server_fetching";
import ProductAdd2Cart from "./ProductAdd2Cart";
import { OpenToast } from "../../../components/Toast";


const SearchInput = SearchInput_Cmpt();
const AddProduct = AddProduct_Cmpt();


const ProductItem = ProductItem_Cmpt();

const TabsHRM = TabsHRM_Cmpt();

const AddEntryProduct = AddEntryProduct_Cmpt();
const AddOutProduct = AddOutProduct_Cmpt();

const ButtonImageRound = ButtonImageRound_Cmpt();


const coll2list = "coll2listprods"



const frmNm = "birthcertificate"

const ProductDashboard  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const [view, set_view] = useState(0);
	const [loading, set_loading] = useState(false);

	const [range, set_range] = useState(128);



	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
			//getall_product(cb_result);
			updStore("products_list",null);
			get_balances_by_type("global_accounts","global_account_params",updObs);
			loadList();
		}
	});


	const openUpdateView =  () => {
		
	}


	



	
	const openAddView =  () => {
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "80vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = 
			<AddProduct />
		
		OpenModal(dataModal);
	}


	

	const handleChange =  (v) => {
		!loading && set_loading(true)
	}


	const handle_search_product =  (v) => {
		set_loading(true)
		search_product(loadList)
	}


	const handle_getall_product =  (v) => {
		//getall_product(cb_result)
		updStore("products_list",null);
		loadList();

	}

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
	};


	const openAddEntry =  () => {
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "80vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = 
			<AddEntryProduct />
		
		OpenModal(dataModal);
	}


	const openOutView =  (iit) => {
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "90vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = 
			<ProductAdd2Cart item={iit} confirm={add2Cart}/>
		
		OpenModal(dataModal);
	}

	const add2Cart=  (v) => {
		OpenToast({ text: v.productName + "  added to cart", timeout: 1500 });
	}

	const loadMore=  () => {
		set_range(v=>v+128)
	}

	const loadList =  () => {
		let docdata = {...getStore("products_list")};
		updStore(coll2list,docdata);
		cb_result();
	};


	return (

	<div className="">
			
		<div className="accounts_header">
			<h2>Products</h2>
			<div className="_dsplFlx ">
				{hasWritePrivilege()?
				<p className="group_title addBtn purple" onClick={openAddView}>
					Agregar Producto
				</p>
				:null}
				{getStore("profileUser").isAdmin?
				<p className="group_title addBtn purple" onClick={()=>set_view(!view)}>
					Cambiar Vista
				</p>
				:null}
			</div>
		</div>
		<div className="_dsplFlx " style={{marginTop:14}} >
			<SearchInput
				placeholder={"Buscar ...."} 
				width={560}
				obs={set_obs}
				fetchData={handle_search_product}
				loading={loading}
				change={handleChange}
				callempty={handle_getall_product}
				keyFlds={"searchQry"}
			/>
			<div className="flexSpace"/>
			<div className="_dsplFlx ">
				<div className="total_comprobante_title">
					No de Productos: 
				</div>
				<div className="total_comprobante gray">
					<span>
						{ObjectKeys(getStore("products_list"))?.length}
					</span>
				</div>
				
			</div>
		</div>

		{view ? 
		
		<div className="products_container scollVh FlexList ">

		{getStore(coll2list) && rangeProdArr(getStore(coll2list),range)?.map(cid=>{
				let data= getStore(coll2list)[cid];
				return (
					<div style={{padding: 9 }} onClick={()=>openOutView(data)}>
						<ButtonImageRound 
							src={data?. imageUrl} 
							width={160} 
							lbl={data?. name} 
							fontSize={[12,16]}
							key={cid}
						/>
					</div>
				)
			})}
		</div>:
		<div className="products_container scollVh">

			{getStore(coll2list) && rangeProdArr(getStore(coll2list),range)?.map(cid=>{
					return (
						<ProductItem 
							data={getStore(coll2list)[cid]} 
							elmId={cid} 
							key={cid}
							openUpdateView={openUpdateView}
							handleRefreshAll={handle_getall_product}
							//inReview={inReview}
							//hasError={hasError}
							updRvw={set_obs}
						/>
					)
				})}
				{getStore(coll2list) && ObjectKeys(getStore(coll2list),range).length >range?
				<div className="_dsplFlx " style={{margin: "15px 0 35px"}}>
					<div className="flexSpace"/>
					<p className="group_title addBtn purple" onClick={loadMore}>
							Cargar Mas
					</p>
					<div className="flexSpace"/>
				</div>
				:null}
		
		</div>
	}
		

	</div>
	);
};

export default ProductDashboard;


