
import React, { useState } from "react";

import "./styles.css"
import { CloseModal } from "../../../components/DialogHRM";
import { ButtonImageRound_Cmpt, SvgIcon_Cmpt } from "../../../components";



const ButtonImageRound = ButtonImageRound_Cmpt();


const Svg = SvgIcon_Cmpt();

const ProductAdd2Cart  = ({ data, confirm, label, item }) => {

	const { modalID } = data;

	const [amount, set_amount] = useState(1);

	const handleClose =  (v) => {
		CloseModal({id:modalID})
    }
    
    const handleConfirm =  () => {
		
        confirm({productId: item.productId , amount, productName:item.name });
		handleClose();
	}

	const plus =  () => {
		set_amount(prev=>prev+1)
	}

	const minus =  () => {
		if(amount-1>=0){
			set_amount(prev=>prev-1)
		}
	}

/// 041331040082

    return (
       
		<div style={{
			maxHeight: "60vh",
			minHeight:  "28vh",
			width:"560px",
			overflow: "auto",
			background: "#fff",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="code_prod_header">
					{item?. productCode}
				</p>
				<div className="flexSpace"/>
				<p className="lbl_prod_header">
					{item?. name}
				</p>
			</div>
		

			<div  className="_dsplFlx "  style={{padding: 9 }}>

				<div>
					<ButtonImageRound 
						src={item?. imageUrl} 
						width={150}
						fontSize={[12,16]}
					/>
				</div>
				
				<div className="flexSpace"/>
				<div className="flexSpace"/>
				<div className="_dsplFlx btns_control_2cart" >
					{amount-1>0?
					<div className="iconFlx ">
						<div  onClick={minus}>
							<Svg name={"minus"} color={"#80868b"} size={48}/>
						</div>
					</div>
					:
					<div className="iconFlx " >
						<div style={{width:52}}/>
					</div>
					}
					<div className="iconFlx lbl_prod_amount">
						{amount}
					</div>
					<div className="iconFlx" >
						<div  onClick={plus}>
							<Svg name={"plus"} color={"#80868b"}  size={48}/>
						</div>
					</div>
				</div>
			</div>
			<div className="_dsplFlx " style={{padding:"1px 10px"}}>
				<p className="lbl_prod_price">
					${item?. price}
				</p>
				<div className="flexSpace"/>

				<div className="_dsplFlx ">
					<div className="iconFlx">
						<p className="group_title addBtn iconFlx" onClick={handleClose}>
							Cancelar
						</p>
					</div>
					<div className="iconFlx">
						<p className="group_title addBtn purple iconFlx" onClick={handleConfirm}>
							Agregar
						</p>
					</div>
				</div>
			</div>

		</div>
    )   
}

export default ProductAdd2Cart;


